document.addEventListener("DOMContentLoaded", function() {
    iniciarApp();
})

function iniciarApp() {
    crearGaleria();
    navFijo();
}

function navFijo() {
    const barra = document.querySelector('.header')
    const video = document.querySelector('.overlay')
    const body = document.querySelector('body')

    window.addEventListener('scroll', function() {
        if (video.getBoundingClientRect().bottom <= 0) {
            barra.classList.add('barraFija');
            body.classList.add('body-scroll')
        }
        else {
            barra.classList.remove('barraFija')
            body.classList.remove('body-scroll')
        }
    })
}

function crearGaleria() {
    const galeria = document.querySelector('.imagenes-galeria');

    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <picture class="pictures">
                <source srcset="/build/img/thumb/${i}.webp" type="image/webp">
                <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="">
            </picture>
        `;

        imagen.addEventListener("click", function() {
            mostrarImagen(i);
        })

        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <picture class="pictures">
                <source srcset="/build/img/thumb/${id}.webp" type="image/webp">
                <img loading="lazy" width="200" height="300" src="build/img/thumb/${id}.jpg" alt="">
                <button class="cerrarImagen">X</button>
            </picture>
        `;

    const overlay = document.createElement('div')
    overlay.appendChild(imagen)
    overlay.classList.add('overlay')

    const body = document.querySelector('body')
    body.appendChild(overlay)
    body.classList.add('scroll-no')

    const boton = document.querySelector('.cerrarImagen')
    boton.addEventListener("click", function() {
        body.removeChild(overlay)
        body.classList.remove('scroll-no')
    })
}

